#dining {
	#{headings(1,2)} {
		color:$red;
		padding: 10px 0;
	} //h1, h2
	img {
		width: 100%;
		border-radius: 10px;
	} //img
	@include breakpoint($small) {
		#areadining {
			margin-bottom: 30px;
			h1 {
				font-weight: $thin;
				font-size: 2.5em;
			} // h1
			p {
				font-size: 1.5em;
				line-height: 140%;
			} //p
		} //areadining

		section {
			@include span(4 of 12);
			h2 {
				font-size: 1.2em;
				line-height: 120%;
				color: $pink;
			} //h2
			p {
				padding-top: 10px;
				font-size: 1em;
				line-height: 130%;
			} //p
		} //section
	} //breakpoint $small
	#roomservice {
		margin-top: 20px;
		background:linear-gradient(to bottom, $light, lighten($yellow, 20));
		@include pad(1, 1);
		text-align: center;
		border-top: 1px solid $red;
		border-bottom: 1px solid $red;
	} //roomservice
}  //dinning